export default {
	data() {
		return {
			StatusAddNav:this.StatusAddNav,
			StatusAddHalfNav:this.StatusAddHalfNav,
			opacityNum:0,
			currentGoodsId:0,
			goodsInfo:{},
			
			cartInfo:{},
			orderKey:'',
			defaultAddress:{},
			
			providerList: [],	//支付列表
			
			orderId:'',			//订单Id编码
			id:'',				//订单id
		}
	},
	
	onPageScroll(e) {
		console.log(e);
		this.opacityNum = e.scrollTop >50? 1:0;
	},
	onLoad(e) {
		this.currentGoodsId = e.id;
		this.getTreeInfo();

































	},
	methods: {
		//支付成功之后修改订单状态
		updatePayStatus(){
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'update_pay_status',
					q: {
						order_id: that.orderId,
						product_id:that.currentGoodsId,
						id:that.id
					}
				}),
				function(res) {
					console.log(res);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		async requestPayment() {
			console.log('uni-支付宝');
		    this.providerList[0].loading = true;
		    let orderInfo = await this.getOrderInfo_uniapp(this.providerList[0].id);
		    console.log("得到订单信息", orderInfo);
		    if (orderInfo.statusCode !== 200) {
		        console.log("获得订单信息失败", orderInfo);
		        uni.showModal({
		            content: "获得订单信息失败",
		            showCancel: false
		        })
		        return;
		    }
		    uni.requestPayment({
		        provider: this.providerList[0].id,
		        orderInfo: orderInfo.data,
		        success: (e) => {
		            console.log("success", e);
		            uni.showToast({
		                title: "感谢您的赞助!"
		            })
		        },
		        fail: (e) => {
		            console.log("fail", e);
		            uni.showModal({
		                content: "支付失败,原因为: " + e.errMsg,
		                showCancel: false
		            })
					that.updatePayStatus();
					
		        },
		        complete: () => {
		            this.providerList[0].loading = false;
		        }
		    })
		},
		
		getOrderInfo_uniapp(e) { 
		    let appid = "";



		    let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=0.1' ;
			console.log(url);
		    return new Promise((res) => {
		        uni.request({
		            url: url,
		            success: (result) => {
		                res(result);
		            },
		            fail: (e) => {
		                res(e);
		            }
		        })
		    })
		},
		//整理接口返回的数据
		SortData(data){
			let map={};	//存在mer_id
			let array=[];	//所有的数据
			for(var i=0;i<data.length;i++){
				var temp=data[i];
				if(!map[temp.mer_id]){
					array.push({
						count_price:temp.productInfo.price*temp.cart_num,	// 价钱*个数=单个总价
						count_num:temp.cart_num,
						mer_id:temp.mer_id,
						data:[temp],
					})
					map[temp.mer_id]="Occupies a position";
				}else{
					for(var j = 0; j < array.length; j++){
						var existData = array[j];
						if(existData.mer_id == temp.mer_id){
							existData.count_price+=temp.productInfo.price*temp.cart_num,
							existData.count_num+=temp.cart_num
							existData.data.push(temp);
							break;
						}
					}
				}
			}
			return array;
		},
		// //获取默认收货地址
		// getAddressList(){
		// 	let that=this;
		// 	that.basePost(
		// 		that.U({ c: 'user_api', a: 'user_default_address'}),
		// 		{},
		// 		function(res) {
		// 			that.defaultAddress=res.data;
		// 		},
		// 		function(res) {
		// 			console.log(res);
		// 		},
		// 	);
		// },
		//创建订单编号
		settlement(){ 
			let that=this;
			that.basePost(
				that.U({ c: 'auth_api', a: 'create_order'}),
				{
					mer_id:that.cartInfo[0].mer_id,
					key:that.orderKey,
					couponId:'',
					userIntegral:'',
					mark:'',
					type:2,
					product_id:that.cartInfo[0].data[0].productInfo.id
				},
				function(res) {  
					that.orderId=res.data.result.orderId;
					that.id=res.data.result.id;
					// that.payModalFlag=true;
					let orderInfo={
						order_id:res.data.result.orderId,
						total_price:that.sumPrice
					}
					that.updatePayStatus();
					//
					// 	uni.navigateTo({
					// 		url:"/pages/user/confirm_payment/confirm_payment?orderInfo="+JSON.stringify(orderInfo)
					// 	})
					//
					



					
				},  
				function(res) {
					console.log(res);
				}
			);
		},
		//用茶树商品的购物车id生成一个订单
		getOrderInfo(cartId){
			let that = this;
			that.basePost(
				that.U({ c: 'auth_api', a: 'confirm_order' }),
				{
					cartId:cartId
				},
				function(res) { 
					console.log(res);
					that.cartInfo = that.SortData(res.data.cartInfo);
					that.orderKey=res.data.orderKey;
					that.settlement();
				},
				function(res) {
					console.log(res);
				}
			);
		},
		// 立即购买
		purchase() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'auth_api',
					a: 'now_buy',
					q: {
						productId: that.goodsInfo.storeInfo.id,
						merId: that.goodsInfo.merInfo.id,
						cartNum: 1
					}
				}),
				function(res) {
					that.getOrderInfo(res.data.cartId);
				},
				function(res) {
					if(res.msg.indexOf("该产品库存不足")!=-1){
						that.Tips({title:res.msg});
					}
				},
				true
			);
		},
		//获取茶树信息进行展示
		getTreeInfo(){
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'details',
					q: {
						id: that.currentGoodsId
					}
				}),
				function(res) {
					that.goodsInfo = res.data;
					console.log(that.goodsInfo);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToBack(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
